import React, { Component } from 'react'
import { Accordion, Icon } from 'semantic-ui-react'
import './styles.css'

export default class AccordionExampleFluid extends Component {
  
  state = { activeIndex: 0 }

  handleClick = (e, titleProps) => {
    const { index } = titleProps
    const { activeIndex } = this.state
    const newIndex = activeIndex === index ? -1 : index

    this.setState({ activeIndex: newIndex })
  }

  render() {
    const { activeIndex } = this.state
    
    return (
      
      <Accordion fluid styled backImage>
        <Accordion.Title
          active={activeIndex === 0}
          index={0}
          onClick={this.handleClick}
        >
          <Icon name='dropdown' />
          What tech stack would you choose if you had to set up a new web application project from scratch?
And why? 

        </Accordion.Title>
        <Accordion.Content active={activeIndex === 0}>
          <p>
          I would choose the MERN stack. It consists of four best technologies : MongoDB, ExpressJS, React, and NodeJS. React uses JSX, which is a modification of JavaScript, enabling consistent component work.For smaller applications React is a great choice which is significantly faster.
          </p>
        </Accordion.Content>

        <Accordion.Title
          active={activeIndex === 1}
          index={1}
          onClick={this.handleClick}
        >
          <Icon name='dropdown' />
          What's the best way of creating a maintainable code base? 


        </Accordion.Title>
        <Accordion.Content active={activeIndex === 1}>
          <p>
          First : Following a consistent coding standard<br/>
2nd :  Naming the variables,functions and methods properly and recognisable even if it makes them long<br/>
3rd : The code should be expressive as if we are speaking<br/>
4th : Avoid creating long methods



          </p>
        </Accordion.Content>

        <Accordion.Title
          active={activeIndex === 2}
          index={2}
          onClick={this.handleClick}
        >
          <Icon name='dropdown' />
          How do you usually implement CSS? (architectures, frameworks, preprocessors, ....)
        </Accordion.Title>
        <Accordion.Content active={activeIndex === 2}>
          <p>
          There are two CSS frameworks that I usually use : Bootstrap and Semantic UI
          <br/>
          And I use LESS and SASS as CSS preprocessors.
          </p>

        </Accordion.Content>
        <Accordion.Title
          active={activeIndex === 3}
          index={3}
          onClick={this.handleClick}
        >
          <Icon name='dropdown' />
          How do you test cross-browser compatibility of your web application?


        </Accordion.Title>
        <Accordion.Content active={activeIndex === 3}>
          <p>
          I have some experience with LambdaTest  and Turbo Browser Sandbox.

          </p>

        </Accordion.Content>
        <Accordion.Title
          active={activeIndex === 4}
          index={4}
          onClick={this.handleClick}
        >
          <Icon name='dropdown' />
          How do you create accessible web applications?


        </Accordion.Title>
        <Accordion.Content active={activeIndex === 4}>
          <p>I use correct headings to organise the structure of the content<br/>
I always include a proper alt text for my images <br/>
I give relevant names to the links<br/>
I make sure that all content is accessible with the keyboard alone in a logical way and dynamic content is accessible.
          </p>

        </Accordion.Content>
        <Accordion.Title
          active={activeIndex === 5}
          index={5}
          onClick={this.handleClick}
        >
          <Icon name='dropdown' />
          What can you do to improve application performance?

        </Accordion.Title>
        <Accordion.Content active={activeIndex === 5}>
          <p>
          Minimizing the number of HTTP requests in a web page.<br/>
          Minifying our files(HTML,CSS,JavaScript,...).<br/>
          Loading files asynchronously can speed up the pages because when a browser loads a page, it moves from top to bottom.<br/>
          Deferring JavaScript files which prevent them from loading until after other elements have loaded. By Deferring larger files, like JavaScript, I ensure that the rest of my content can load without a delay.<br/>
          Minimizing the TTFB(the amount of time a browser has to wait before getting its first byte of data from the server).<br/>
          Switching to a faster DNS provider.<br/>
          Choosing the right hosting option.<br/> 
          Trying to keep my files as small as it can be without affecting the quality.<br/>
          Enable browser caching.<br/>
          Reducing the size of the images in web pages.<br/>
          Using CDNs.<br/>
          Using only external CSS.<br/> 

          </p>

        </Accordion.Content>
        <Accordion.Title
          active={activeIndex === 6}
          index={6}
          onClick={this.handleClick}
        >
          <Icon name='dropdown' />
          What can you do to improve application quality?

        </Accordion.Title>
        <Accordion.Content active={activeIndex === 6}>
          <p>
          Prototyping.<br/>	
          Using high	quality	components.<br/>
          Checking	the	design	or	the	code	or	walking	through	the	code	with	a	few	peers.<br/>
          Code inspecting,	peer	reviews,	customer	reviews.<br/>
          Comparing	what	should	the	application	do	and	what	it	actually	does.<br/>
          Controling the technical	character	of the application with Guidelines.<br/>
          
          </p>

        </Accordion.Content>
        <Accordion.Title
          active={activeIndex === 7}
          index={7}
          onClick={this.handleClick}
        >
          <Icon name='dropdown' />
          What are the last 3 things you searched for on Stack Overflow?

        </Accordion.Title>
        <Accordion.Content active={activeIndex === 7}>
          <p>
          How to call two function in one hook in react.<br/>
          Is it safe to use Flexbox and Grids together in CSS?<br/>
          Where is the best place for API calls in react?<br/>
          </p>

        </Accordion.Content>
        <Accordion.Title
          active={activeIndex === 8}
          index={8}
          onClick={this.handleClick}
        >
          <Icon name='dropdown' />
          What feature(s)/aspect of the React library do you enjoy the most?

        </Accordion.Title>
        <Accordion.Content active={activeIndex === 8}>
          <p>
          It's free and open source.<br/>
          It has reusable components.<br/>
          JSX makes mixing HTML and JavaScript much easier and increases code reliability.<br/>
          It has large supporting community.



          </p>

        </Accordion.Content>
        <Accordion.Title
          active={activeIndex === 9}
          index={9}
          onClick={this.handleClick}
        >
          <Icon name='dropdown' />
          What feature(s)/aspect of the React library do you enjoy the least?

        </Accordion.Title>
        <Accordion.Content active={activeIndex === 9}>
          <p>
          It's a library, not a framework.<br/>
          It handles the UI only. You need to use other libraries for other parts of your application.

          </p>

        </Accordion.Content>
        
        
      </Accordion>
    )
  }
  
}
